* {
  box-sizing: border-box;
}

body {
  background-color:darkslategray;
}

#drum-machine {
  width: 700px;
  height: 400px;
  margin: 0 auto;
  background-color: #7f868f;
  display: flex;
  flex-direction: row;
  margin-top: 8rem;
  border: 5px solid #5f646b;
  box-shadow: 10px 10px black;
}

#flex-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;
}

#button-wrapper {
  display: grid;
  grid-template-columns: 80px 80px 80px;
  grid-template-rows: 80px 80px 80px;
  column-gap: 0.8rem;
  row-gap: 0.8rem;
}

.drum-pad {
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
}

.hotkey {
  font-size: 2rem;
  margin: auto;
  color: chartreuse;
  font-family: 'Press Start 2P', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

#controls {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 1rem;
}

#name-plate {
  background-color: black;
  height: 3rem;
  width: 90%;
  height: 5rem;
  margin-top: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 5px solid #5f646b;
}

#name-plate > h2 {
  text-align: center;
  margin: 0;
  color: chartreuse;
  font-family: 'Press Start 2P', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

#volume-control {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#volume-control > p {
  text-align: center;
  font-weight: bold;
}

#volume-slider {
  -webkit-appearance: none;
  appearance: none;
  width: 80%;
  background: #52514e;
  height: 0.5rem;
}

#volume-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 10px;
  height: 20px;
  background: #242323;
  cursor: pointer;
}

#display {
  background-color: black;
  width: 90%;
  height: 15%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 5px solid #5f646b;
}

#display > p {
  text-align: center;
  color: chartreuse;
  font-family: 'Press Start 2P', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}